import { useState } from "react";

import Layout from "../components/Layout";

import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";

function LetterToSanta() {
  const [howBehaved, setHowBehaved] = useState();

  return (
    <section>
      <RadioGroup value={this.state.howBehaved}>
        <Radio value="naughty">Naughty</Radio>
        <Radio value="nice">Nice</Radio>
        <Radio value="veryNice">Very Nice</Radio>
      </RadioGroup>
    </section>
  );
}

export default LetterToSanta;
